.list-box{
    margin-top: 12px;
    width: 100%;
    .list-ul,.InfiniteScroll{
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        row-gap: 28px;
        >li{
            width: 21%;
            background-color: #ebebeb;
            padding-bottom: 25px;
            figure{
                width: 100%;
                margin: 0;
                display: flex;
                align-items: center;
                flex-direction: column;
                row-gap: 8px;
                img{
                    width: 200px;
                    width: 200px;
                }
                figcaption{
                    width: 100%;
                    font-size: 14px;
                    display: flex;
                }
                &>figcaption:nth-of-type(1){
                    color: #c42a16;
                    font-weight: 700;
                    font-size: 18px;
                    justify-content: center;
                }
                &>figcaption:nth-of-type(2){
                    padding-left: 36px;
                    .icon-ul{
                        li{
                            text-align: center;
                        }
                    }
                    .msg-ul{
                        padding-left: 12px;
                        li:nth-child(1){
                            font-weight: 700;
                            a{
                                color: #2c2c2c;
                            }
                        }
                    }
                }
            }
        }
    }
    @media screen and (max-width:1200px) {
        width: 1200px;
    }
}