.instruction{
    width: 100%;
    p{
        text-align: center;
        font-size: 24px;
        font-weight: 700;
    }
    .instruction-ul{
        width: 80%;
        margin: 0 auto;
        display: flex;
        justify-content: space-evenly;
        figure{
            margin: 0;
            display: flex;
            flex-direction: column;
            figcaption{
                text-align: center;
                font-size: 18px;
                font-weight: 700;
                padding: 12px 0;
            }
        }
    }
}