.player{
    width: 100%;
    >p{
        margin-top: 24px;
        text-align: center;
        font-size: 24px;
        font-weight: 700;
    }
    .player-ul{
        width: 80%;
        margin: 0 auto;
        display: flex;
        column-gap: 12px;
        >li{
            flex: 1;
            text-align: center;
            h3{
                font-size: 18px;
                font-weight: 700;
            }
            form{
                display: flex;
                justify-content: center;
                gap: 12px;
                input{
                    &:nth-child(1){
                        outline-color: #1890ff;
                        border: none;
                        border: 1px solid #767676;
                    }
                }
            }
        }
    }
}